import { TESTDATA, TESTDATA_LONG } from './testdata';
import { SearchPicker } from "../src";
import { CustomField } from "./CustomField";
import { CustomRenderers } from "./CustomRenderers";
document.addEventListener("DOMContentLoaded", function (event) {
    var searchPicker = new SearchPicker(document.getElementById('searchpicker'), {
        placeholder: 'Sample placeholder',
        source: TESTDATA,
        minLengthToSearch: 0
    });
    bindPickerResultOutput(searchPicker);
    var searchPicker2 = new SearchPicker(document.getElementById('searchpicker-singlechoice'), {
        placeholder: 'Only 1 choice',
        source: TESTDATA,
        minLengthToSearch: 0,
        maxSelectedChoices: 1
    });
    bindPickerResultOutput(searchPicker2, 'singlechoice');
    var searchPicker3 = new SearchPicker(document.getElementById('searchpicker-unlim'), {
        placeholder: 'Unlimited choices, search from 3 symbols',
        source: TESTDATA,
        minLengthToSearch: 3
    });
    bindPickerResultOutput(searchPicker3, 'unlim');
    var searchPicker4 = new SearchPicker(document.getElementById('searchpicker-top3'), {
        placeholder: 'Show only top3 results, max 3 choices',
        source: TESTDATA,
        resultsLimit: 3,
        maxSelectedChoices: 3
    });
    bindPickerResultOutput(searchPicker4, 'top3');
    var searchPicker5 = new SearchPicker(document.getElementById('searchpicker-cutlong'), {
        placeholder: 'Cut long text',
        source: TESTDATA_LONG,
        minLengthToSearch: 0,
    });
    bindPickerResultOutput(searchPicker5, 'cutlong');
    var searchPicker6 = new SearchPicker(document.getElementById('searchpicker-footer'), {
        placeholder: 'Footer',
        source: TESTDATA,
        minLengthToSearch: 0,
        resultsLimit: 3,
        resultFooterRenderer: CustomRenderers.footerRenderer
    });
    bindPickerResultOutput(searchPicker6, 'footer');
    new CustomField("#searchpicker-customfield");
});
function bindPickerResultOutput(picker, name) {
    var resHolder = document.getElementById(name ? 'searchpicker-result-' + name : 'searchpicker-result');
    picker.on("choiceAdded", function () {
        resHolder.innerHTML = picker.getChoices().map(function (elm) { return elm.id; }).join(",");
    });
    picker.on("choiceRemoved", function () {
        resHolder.innerHTML = picker.getChoices().map(function (elm) { return elm.id; }).join(",");
    });
}
